
/* HEARTBEAT ANIMATION BY  */
@keyframes heartbeat {
    0% { font-size: 1em; }
    8% { font-size: 0.8em;  }
    17% { font-size: 1.2em; }
    25% { font-size: 0.8em; }
    87.5% { font-size: 0.8em; }
    100%  { font-size: 1em; }
}

@keyframes shake {
    10%,30%,50%,70%,90% { transform:  rotate(-4deg) translate(0px, 0px) scaleX(-1); }
	20%,40%,60%,80%,100%,0% { transform: rotate(4deg) translate(-1px, -1px) scaleX(-1); }
}

@keyframes rotate {
	0% { transform:  rotate(0deg) }
	100% { transform:  rotate(360deg) }
}	

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes locate {
    0% { transform:  rotate(0deg); }
    25% { transform:  rotate(45deg); }
    50% { transform:  rotate(-45deg); }
    75% { transform:  rotate(-90deg); }
    100% { transform:  rotate(0deg); }
}   


/* jungle top */
@keyframes jungle_movetop {
    from    {top: 0%;}
    to      {top: -3%;}
}


/* jungle right side */
@keyframes jungle_moverightbg {
    from    {right: 0%;}
    to      {right: -4%;}
}

@keyframes jungle_moveright {
    from    {
        top: -15%;
        right: -10%;
        transform: rotate(-25deg);
    }
    to      {
        top: 5%;
        right: -25%;
        transform: rotate(-35deg);
    }
}

/* jungle left side */

@keyframes jungle_moveleftbg {
    from    {left: 0%;}
    to      {left: -4%;}
}

@keyframes jungle_moveleft {
    from    {
        top: -10%;
        left: -10%;
        transform: rotate(25deg);
    }
    to      {
        top: 0%;
        left: -28%;
        transform: rotate(40deg);
    }
}